import { Storage } from "@ionic/storage";
import { HttpClient } from "@angular/common/http";
import { Toast } from "@ionic-native/toast";
import { Component } from "@angular/core";
import { NavController, NavParams, LoadingController } from "ionic-angular";
import { Device } from "@ionic-native/device";

declare var PaystackPop: any;
import genVoucher from "voucher-code-generator";


@Component({
  selector: "page-voucher-check",
  templateUrl: "voucher-check.html"
})
export class VoucherCheckPage {
  expired = false;
  voucher: string;
  voucherType: any;
  user: any;

  constructor(
    public navCtrl: NavController,
    private navParams: NavParams,
    private toast: Toast,
    private loadingCtrl: LoadingController,
    private http: HttpClient,
    private storage: Storage,
    private device: Device
  ) {
    this.expired = this.navParams.data.expired;
    this.storage.get("user").then(user => (this.user = user));
  }

  randString(x) {
    let s = "";
    while (s.length < x && x > 0) {
      var r = Math.random();
      s +=
        r < 0.1
          ? Math.floor(r * 100)
          : String.fromCharCode(Math.floor(r * 26) + (r > 0.5 ? 97 : 65));
    }
    let n = s + "" + Math.floor(Math.random() * 1000000000);
    return n;
  }

  verifyVoucher() {
    if (this.voucher === "") {
      this.toast
        .showLongBottom("You need a voucher code to play/download the sermon")
        .subscribe(_ => {});
    } else {
      let load = this.loadingCtrl.create({
        content: "Verifying voucher, please wait...",
        spinner: "dots"
      });
      load.present();
      this.http
        .post("https://realm-glory.herokuapp.com/api/voucher/verify", {
          voucher: this.voucher.toUpperCase(),
          device: this.device.uuid,
          user: this.user ? this.user._id : null
        })
        .subscribe(
          data => {
            load.dismiss();
            const status = data["status"];
            console.log("feedback", data);
            switch (status) {
              case "invalid":
                this.toast
                  .showLongBottom(
                    "Invalid voucher code, check the code and try again."
                  )
                  .subscribe(_ => {});
                break;
              case "expired":
                this.toast
                  .showLongBottom(
                    "This voucher has expired. Please purchase a new voucher."
                  )
                  .subscribe(_ => {});
                break;
              case "userExpired":
                this.storage.set("user", data["data"]).then(() => {
                  this.toast
                    .showLongBottom(
                      "This voucher has expired. Please purchase a new voucher."
                    )
                    .subscribe(_ => {});
                });
                break;
              case "voucherExpired":
                this.storage.set("voucher", data["voucher"]).then(() => {
                  this.toast
                    .showLongBottom(
                      "This voucher has expired. Please purchase a new voucher."
                    )
                    .subscribe(_ => {});
                });
                break;
              case "voucher":
                this.storage.set("voucher", data["data"]).then(_ => {
                  this.navCtrl.setRoot("TabsPage");
                });
                break;
              case "userSuccess":
                this.storage.set("user", data["user"]).then(_ => {
                  this.navCtrl.setRoot("TabsPage");
                });
                break;
              case "voucherSuccess":
                this.storage.set("voucher", data["data"]).then(_ => {
                  this.navCtrl.setRoot("TabsPage");
                });
                break;
              default:
                break;
            }
            // if (status['status'] === 'usedup') {
            //   this.toast.showLongBottom('You have reached your usage limit for this voucher, please purchase a new voucher.').subscribe(_ => {});
            // }
          },
          err => {
            load.dismiss();
            console.log("err", err);
          }
        );
    }
  }

  buyVoucher() {
    if (this.voucherType === "1") {
      this.payWithPaystack(1000);
    } else if (this.voucherType === "3") {
      this.payWithPaystack(2500);
    } else if (this.voucherType === "6") {
      this.payWithPaystack(5000);
    } else {
      this.payWithPaystack(10000);
    }
  }

  payWithPaystack(amount) {
    var handler = PaystackPop.setup({
      key: "pk_live_dd30879cfe9d119572fd63a2833c69d16e0d08d7",
      email: this.user ? this.user.email : "no.user@realmofglory.org",
      amount: amount * 100,
      ref: this.randString(10),
      metadata: {
        custom_fields: [
          {
            display_name: "Voucher Type",
            variable_name: "voucher_type",
            value: this.voucherType
          }
        ]
      },
      callback: response => {
        this.paymentMade(response, amount);
      },
      onClose: function() {}
    });
    handler.openIframe();
  }

  paymentMade(response, amount: number) {
    const voucher = genVoucher.generate({
      prefix: "ROG-",
      length: 9,
      count: 1,
      charset: "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ",
      pattern: "###-####-##"
    });
    const data = {
      user: this.user ? this.user._id : null,
      type: this.voucherType,
      voucher: voucher[0],
      amount: amount,
      ref: response.trxref,
      device: this.device.uuid
    };
    this.navCtrl.pop();
    this.navCtrl.setRoot("VoucherPaymentMadePage", { details: data });
  }
}



